import React, { useState } from 'react';

function StepFour ({setNumberStep, handleStep}) {

    const [checkStatus, setCheckStatus] = useState(false);

    const handleValidate = e => {
        setCheckStatus(!checkStatus);
    }

    const btn = document.getElementById("button");
    if(checkStatus){
        btn.removeAttribute("disabled");
        btn.style.opacity = "100%";
    }
    else{
        btn.setAttribute("disabled", true)
        btn.style.opacity = "50%";
    }

    const handleForm = e => {
        e.preventDefault();

        const text = "tus datos";
        const text2 = "han sido enviado con éxito";

        setNumberStep(5);
        handleStep(4, text, text2);
    }

    return (
        <form className="animated fadeIn fast" onSubmit={handleForm}>
            
            <p className="bold mb-3">Por favor revisa nuestros términos y condiciones para este servicio:</p>
            <small id="terms">Consulta Términos y Condiciones</small>

            
            <div className="form-fields terms-fields">
              <label htmlFor="agree-terms">Acepto los Términos y Condiciónes</label>
              <input type="checkbox" id="agree-terms" required placeholder="1234" onChange={handleValidate} />
            </div>

            <div className="flex btn-container">
              <input type="submit" className="btn btn2" id="button" disabled value="Enviar" />
            </div>
          </form>
    );
}

export default StepFour;