import React, { Fragment, useState } from 'react';

import lockImg from '../../assets/img/icons/ic_lock_24px.png';

function StepOne ({setNumberStep, handleProgress, handleStep}) {
    const [data, setData] = useState({
        name: '',
        surname: ''
    });

    let {name, surname} = data;

    //Catching data
    const saveData = e => {
        setData({
            ...data,
            [e.target.name]: e.target.value
        });

        //Validating form
        const button = document.getElementById("button");
        if( name === "" && surname === "" ){
            button.setAttribute("disabled", true);
            button.style.opacity = "50%";
        }
        else{
            button.removeAttribute("disabled");
            button.style.opacity = "100%";
        }
    }

    //Submit data
    const handleSubmit = e => {
        const text = "Valida tu";
        const text2 = "celular";
        e.preventDefault();
        setNumberStep(2);
        handleProgress("37.5%");
        handleStep(1, text, text2);
    }

    return (
        <Fragment>
            <form className="animated fadeIn fast" onSubmit={ handleSubmit }>                        
                <p className="mb-3">Queremos saber que eres tú, por favor ingresa los siguientes datos:</p>

                <div className="form-fields">
                    <label htmlFor="name">Nombre</label>
                    <input 
                        type="text" 
                        name="name" 
                        onChange={ saveData }
                        required 
                    />
                    <img src={lockImg} alt="locked" loading="lazy" />
                </div>
                <div className="form-fields">
                    <label htmlFor="surname">Apellidos</label>
                    <input 
                        type="text" 
                        name="surname" 
                        onChange={ saveData }
                        required 
                    />
                    <img src={lockImg} alt="locked" loading="lazy" />
                </div>
                <div className="flex btn-container">
                    <input 
                        type="submit" 
                        className="btn btn2" 
                        name="submit" 
                        value="Enviar" 
                        id="button"
                        disabled 
                    />
                </div>
            </form>
        </Fragment>
    );
}

export default StepOne;