import React, { useState } from 'react';

function StepTwo ({setNumberStep, handleProgress, handleStep, handlePhoneNumber}) {

    const [phoneNumber, setPhoneNumber] = useState();
    const [error, setError] = useState(false);

    const handleValidate = e => {
        setPhoneNumber(e.target.value);

        const btn = document.getElementById("button");
        if(phoneNumber !== ""){
            
            btn.removeAttribute("disabled");
            btn.style.opacity = "100%";
        }
        else{
            btn.setAttribute("disabled", true)
            btn.style.opacity = "50%";
        }
    }

    const handleForm = e => {
        e.preventDefault();

        //Validacion
        const regExp = /[0-9]{10}/;
        
        if( regExp.test(phoneNumber) ){
            const text = "codigo de";
            const text2 = "verificacion";

            setNumberStep(3);
            handleProgress("62.5%");
            handleStep(2, text, text2);
            handlePhoneNumber(phoneNumber);
        }
        else{
            setError(true);
        }

        
    }

    return (
        <form className="animated fadeIn fast" onSubmit={handleForm}>

            <p className="mb-3"><span className="bold">Necesitamos validar tu número para continuar</span></p>
            <p className="mb-3">Ingresa tu número a 10 dígitos y te enviaremos un código SMS</p>

            
            <div className="form-fields">
              <label>Número de Celular</label>
              <input 
                type="text" 
                name="cellphone" 
                required 
                placeholder="55 1234 5678" 
                onChange={handleValidate}
            />
            <small className="orange">{error? "Por favor ingresa un número a 10 dígitos": null}</small>
            </div>
            <div className="flex btn-container">
              <input type="submit" className="btn btn2" name="form2" value="Continuar" id="button" disabled />
            </div>
          </form>
    );
}

export default StepTwo;