import React, {Component, createRef} from 'react';
import StepsBar from './StepBar';


import StepsController from './StepsController';

import astronautImg from '../../assets/img/bgd/Group_4033.png';


class Steps extends Component {
    constructor(){
        super();
        this.bar = createRef();
        this.state = {
            phoneNumber: ""
        }
    }

    handlePhoneNumber = phoneNumber =>{
        this.setState({
            phoneNumber
        });
    }

    handleProgress = progress => {
        this.bar.updateBar(progress);
    }
    handleStep = (number, text, text2) => {
        this.bar.updateStep(number, text, text2);
    }

    render() {
        return (
            <section className="datos">
            <div className="flex container">

                <div className="form form-step-1">

                    <StepsBar 
                        ref={element => this.bar = element}
                    />
                
                    <StepsController 
                        handleProgress={this.handleProgress}
                        handleStep={this.handleStep}
                        handlePhoneNumber={this.handlePhoneNumber}
                        phoneNumber={this.state.phoneNumber}
                    />

                </div>

                <div className="astronaut-container">
                    <img src={astronautImg} alt="astronaut" loading="lazy" />
                </div>
            </div>
        </section>
        );
    }
}

export default Steps;