import React, { useState } from 'react';
import image from '../../assets/img/icons/Group_4026.png';

function StepThree ({setNumberStep, handleProgress, handleStep, phoneNumber}) {

    const [codeVerify, setcodeVerify] = useState();

    const handleValidate = e => {
        setcodeVerify(e.target.value);

        const btn = document.getElementById("button");
        if(codeVerify !== ""){
            btn.removeAttribute("disabled");
            btn.style.opacity = "100%";
        }
        else{
            btn.setAttribute("disabled", true)
            btn.style.opacity = "50%";
        }
    }

    const handleForm = e => {
      const text = "términos y";
      const text2 = "condiciones";

        e.preventDefault();
        setNumberStep(4);
        handleProgress("100%");
        handleStep(3, text, text2);
    }

    return (
        <form className="animated fadeIn fast" onSubmit={handleForm}>

          <p className="bold">Te enviamos un SMS al número:</p>
          
          <span className="mb-3 flex wth-100">
            <div className="">
            {phoneNumber}
            </div>
            <div className="step-number-edit" step="2">
              <img src={image} step="2" alt="edit" />
            </div>
          </span>
          <p className="mb-3">Ingresa el código de verificación</p>

          
          <div className="form-fields">
            <label htmlFor="cellphone">Código de verificación</label>
            <input type="text" name="code" required placeholder="1234" onChange={handleValidate}/>
          </div>
          <p>¿No recibiste el código? <span className="bold">Reenviar código</span></p>
          <div className="flex btn-container">
            <input type="submit" className="btn btn2" id="button" value="Validar código" disabled />
          </div>
      </form>
    );
}

export default StepThree;