import React, { Component } from 'react';

class StepBar extends Component {

    constructor(props) {
        super(props);

        this.state = {
          showBar: true,
          progressBar: '12.5%',
          number: 1,
          step1: "steps step-actual",
          step2: "steps",
          step3: "steps",
          step4: "steps",
          simple: "steps",
          text1: "Te queremos", 
          text2: "conocer",
          stepStatus: 0
        };
    }

    updateBar(progress){
        this.setState({
            progressBar: progress
        });
    }

    async updateStep(number = 0, text1, text2){
        await this.setState({stepStatus: number});
        await this.updateSteps();
    }

    async updateSteps(){

        if (this.state.stepStatus === 1) {
            this.setState({
                step1: "steps step-checked",
                step2: "steps step-actual",
                text1: "Valida tu",
                text2: "celular",
                number: 2
            })
        }
        if (this.state.stepStatus === 2) {
            this.setState({
                step1: "steps step-checked",
                step2: "steps step-checked",
                step3: "steps step-actual",
                text1: "codigo de",
                text2: "verificación",
                number: 3
            })
        }
        if (this.state.stepStatus === 3) {
            this.setState({
                step1: "steps step-checked",
                step2: "steps step-checked",
                step3: "steps step-checked",
                step4: "steps step-actual",
                text1: "términos y",
                text2: "condiciones",
                number: 4
            })
        }
        if (this.state.stepStatus === 4) {
            this.setState({
                step1: "steps step-checked",
                step2: "steps step-checked",
                step3: "steps step-checked",
                step4: "steps step-checked",
                text1: "tus datos",
                text2: "han sido enviado con exito",
                showBar: false
            })
        }      
    }
   
    render() {
        if (this.state.showBar) {
            return (
                <div className="container-prog-bar animated fadeIn fast">
                    <div className="steps-container flex">
                        <span className={this.state.step1}>1</span>
                        <span className={this.state.step2}>2</span>
                        <span className={this.state.step3}>3</span>
                        <span className={this.state.step4}>4</span>
                    </div>
                    <div className="prog-bar">
                        <div className="loaded-prog-bar" style={{transition: 'all .3s ease', width: this.state.progressBar}}></div>
                    </div>
                    <div className="flex step-number-container">
                        <span className="step-number">{this.state.number}</span>
                        <h1 className="title emphasis">{this.state.text1}<span className="orange"> {this.state.text2}</span></h1>
                    </div>
                </div>
                );
        }
        else{
            return null;
        }
        
    }
}

export default StepBar;